	methods: {
		getInfo(event){
			// console.log('data',data);
			let {rawData} = event.detail;
			if(rawData){
				uni.setStorageSync('userInfo',rawData);
				uni.reLaunch({
					url:"/pages/personal/personal"
				})
			}
		},
	}
}
